import React from 'react'
import './style.less'
import Footer from '../../components/Footer/Footer.jsx'

class Order extends React.Component {
    constructor(props,context) {
        super(props,context)
    }

    render() {
        return (
            <div className="order">
                Order 开发中
                <Footer footer="order"/>
            </div>
        )
    }
}

export default Order


