import React from 'react'
import './style.less'
import Footer from '../../components/Footer/Footer.jsx'
import MineHead from '../../components/MineHead/MineHead.jsx'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actionsFromOtherFile from '../../actions/userinfo.js'
import Login from './Login/Login.jsx'
import Info from './Info/Info.jsx'

class Mine extends React.Component {
    constructor(props,context) {
        super(props,context)
    }

    render() {
        console.log(this.props.userinfo.isLogin)
        return (
            <div className="mine">
                <MineHead />
                {this.props.userinfo.isLogin
                ?<Info />
                :<Login />
                }
                <Footer footer="mine"/>
            </div>
        )
    }

    // componentDidMout
}


// export default Mine
//---------------react-redux关联绑定------------------

// function mapStateToProps(state) {
//     userlogin: state.userlogin
// }

// function mapDispatchToProps(dispatch) {
//     userinfoActions: bindActionCreators(userinfoActionsFromOtherFile,dispatch)
// }

// export default connect(
//     mapStateToProps,
//     mapDispatchToProps
// )(Mine)

function mapStateToProps(state) {
    return {userinfo: state.userinfo}
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(actionsFromOtherFile,dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Mine)


